<template>
  <div>
    <navInternment/>
    <div class="container bg-light">
      <b-modal id="my-modal" ref="myModalRef" hide-footer title="Internar Paciente">
        <div class="container">
          <div class="row">
            <p>¿Está seguro que desea internar al paciente?</p>
            <div class="col-md-6 pl-0">
              <a
                href="#"
                @click="save"
                class="btn btn-secondary btn-sm btn-block"
              >Confirmar</a>
            </div>
            <div class="col-md-6 pr-0">
              <a
                href="#"
                @click="$refs.myModalRef.hide()"
                class="btn btn-tertiary btn-sm btn-block"
              >Cancelar</a>
            </div>
          </div>
        </div>
      </b-modal>
      <h1 class="text-center text-dark p-5">
        <b>Registro de Internación</b>
      </h1>
      <h5 class="text-center text-dark"><b>Llene el siguiente formulario</b></h5>
      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-4">
            <label for="paciente">Pacientes</label>
            <select class="form-control" id="paciente" v-model.number="paciente.id" required>
              <option disabled value>--Seleccione--</option>
              <option
                v-for="(value,key) in allPatients"
                :key="key"
                :value="value.id"
              >{{value.nombre}} {{value.apellido}}</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="medicos">Médicos</label>
            <select class="form-control" id="medicos" v-model.number="medico.id" required>
              <option disabled value>--Seleccione--</option>
              <option
                v-for="(value,key) in allMedics"
                :key="key"
                :value="value.id"
              >{{value.nombre}} {{value.apellido}} - {{value.especialidad}}</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="motivo">Motivo</label>
            <input class="form-control" placeholder="Motivo" v-model="motivo" type="text" required>
          </div>
        </div>
        <h1 class="p-5 text-dark text-center"><b>Listado de Habitaciones</b></h1>
        <b-row class="p-5">
          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
              <b-input-group>
                <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
                <b-input-group-append>
                  <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
              <b-input-group>
                <b-form-select v-model="sortBy" :options="sortOptions">
                  <option slot="first" :value="null">-- Seleccione --</option>
                </b-form-select>
                <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                  <option :value="false">Asc</option>
                  <option :value="true">Desc</option>
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>
        </b-row>
        <b-table
          id="table-bedroom-state"
          show-empty
          hover
          small
          stacked="xl"
          fixed
          empty-filtered-text="No se encontrarón registros en su búsqueda"
          empty-text="No hay datos para mostrar"
          :items="bedrooms"
          :fields="columns"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
          :current-page="currentPage"
          :per-page="10"
          @filtered="onFiltered"
        >
          <template slot="piso" slot-scope="data">{{data.value.descripcion}}</template>
          <template slot="estadoHabitacion" slot-scope="data">
            <span
              v-if="data.value.descripcion==='Ocupada'"
              class="btn btn-danger"
            >{{data.value.descripcion}}</span>
            <span v-else class="btn btn-success">{{data.value.descripcion}}</span>
          </template>
          <template slot="tipoHabitacion" slot-scope="data">{{data.value.descripcion}}</template>
          <template slot="genero" slot-scope="data">{{data.value.descripcion}}</template>
          <template slot="actions" slot-scope="data">
              <button class="btn btn-primary" type="button" @click="getId(data.item.id)">Registrar</button>
          </template>
        </b-table>
        <b-row class="p-5">
          <b-col md="8" class="my-1">
            <b-pagination
              size="md"
              :total-rows="totalRows"
              v-model="currentPage"
              :per-page="10"
              @input="find(currentPage)"
            ></b-pagination>
          </b-col>
        </b-row>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navInternment from "@/components/internment/navInternment.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "internarPaciente",
  data() {
    return {
      columns: [
        {
          key: "piso"
        },
        {
          key: "tipoHabitacion",
          label: "Tipo",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "genero",
          label: "Género",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "numero"
        },
        {
          key: "estadoHabitacion",
          label: "Estado"
        },
        {
          key:"actions"
        }
      ],
      estadoHabitacion: {
        id: 1
      },
      medico: {
        id: null
      },
      paciente: {
        id: null
      },
      habitacion: {
        id: null
      },
      allPatients: [],
      allMedics: [],
      bedrooms: [],
      bedroom: [],
      motivo: null,
      fechaInternacion: null,
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      currentPage: 1,
      totalRows: null
    };
  },
  methods: {
    async findAllPatients() {
      axios
        .get(`${URL}/api/pacientes`)
        .then(response => {
          this.allPatients = response.data;
          this.allPatients = this.allPatients.filter(patients => {
            if (patients.estaInternado === false) {
              return true;
            }
          });
        })
        .catch(error => console.log(error.message));
    },
    async findAllMedics() {
      axios
        .get(`${URL}/api/medicos`)
        .then(response => (this.allMedics = response.data))
        .catch(error => console.log(error.message));
    },
    async find(currentPage) {
      axios
        .get(`${URL}/api/habitacion/estado/${this.estadoHabitacion.id}?page=${currentPage-1}`)
        .then(response => {
          this.bedroom = response.data.content
          this.totalRows=response.data.totalElements
        })
        .catch(error => console.log(error.message));
    },
    async findAllBedrooms() {
      axios
        .get(`${URL}/api/habitaciones/estado/${this.estadoHabitacion.id}`)
        .then(response => (this.bedrooms = response.data))
        .catch(error => console.log(error.message));
    },
    save(e) {
      e.preventDefault();
      axios
        .post(`${URL}/api/internacion`, {
          paciente: this.paciente,
          medico: this.medico,
          habitacion: this.habitacion,
          motivo: this.motivo,
          fechaInternacion: new Date()
        })
        .then(response => {
          (this.paciente = {}),
            (this.medico = {}),
            (this.habitacion = {}),
            (this.motivo = null),
            (this.fechaInternacion = null),
            bootbox.alert({
              title: "Correcto",
              message: response.data.mensaje,
              locale: "es",
              backdrop: true,
              callback: this.$router.push("/internaciones")
            });
        })
        .catch(error => {
          this.$refs.myModalRef.hide()
          if (error.response.status === 404) {
            bootbox.alert({
              title: "Error",
              message: error.response.data.mensaje,
              locale: "es",
              backdrop: true
            });
            console.log("Error " + error.response.status + " Not Found");
          }
          if (error.response.status === 409) {
            bootbox.alert({
              title: "Error",
              message: error.response.data.mensaje,
              locale: "es",
              backdrop: true
            });
            console.log("Error " + error.response.status + " Conflict");
          }
          if (error.response.status === 500) {
            bootbox.alert({
              title: "Error",
              message: 'Especifique el motivo',
              locale: "es",
              backdrop: true
            });
            console.log("Error " + error.response.status + " Internal server error");
          }
        });
    },
    getId(id) {
      this.habitacion={id:id}
      this.$refs.myModalRef.show();
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    }
  },
  mounted(currentPage) {
    this.findAllMedics();
    this.findAllPatients();
    this.findAllBedrooms();
    this.find(currentPage);
  },
  components: {
    navInternment,
    Footer
  }
};
</script>
